:root {
  --header-height: 64px;
}

.header {
  position: sticky;
  top: 0;
  z-index: 4;
  display: flex;
  width: 100%;
  height: var(--header-height);

  background-color: var(--c-bg);
  border-bottom: 1px solid var(--c-elements);

  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
  }
}